var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"Apresentacao"},[_c('h1',{staticClass:"cap_lei"},[_vm._v("Simplificando o Licenciamento em São Paulo")]),_vm._v(" "),_c('p',[_vm._v("O Código de Obras e Edificações – COE, sancionado pelo Prefeito João Dória em maio de 2017 e em vigor desde julho de 2017, apresenta um novo modelo de licenciamento de edificações. O COE vem para resolver entraves que perduram por anos e dificultam a transformação de São Paulo em sua vocação de cidade dinâmica e global.")]),_vm._v(" "),_c('p',[_vm._v("A primeira conquista deste Código é a simplificação. Com base na experiência administrativa da SMUL em liberar edificações, o texto da lei também detalhou procedimentos internos para facilitar e desburocratizar a análise dos projetos pelos técnicos. A nova lei está menos burocrática e em consonância com as diretrizes do Plano Diretor Estratégico e da Lei de Parcelamento, Uso e Ocupação do Solo.  Visa, assim, deixar os procedimentos e o processo de licenciamento mais simples, tanto para o cidadão, quanto para os técnicos, além de contribuir para o crescimento e desenvolvimento da cidade.")]),_vm._v(" "),_c('p',[_vm._v("Com este COE, os detalhes internos das edificações ficam a critério do proprietário e do autor do projeto, que agora possuem responsabilidades explícitas. Deste modo, a Prefeitura poderá se focar nos aspectos urbanísticos, ambientais, de sustentabilidade, acessibilidade e segurança dos empreendimentos.")]),_vm._v(" "),_c('p',[_vm._v("Hoje, na administração pública, perseguimos o princípio da transparência. Com os regramentos administrativos mais claros, existem poucas exigências técnicas complementares, o que dá agilidade ao processo. Em relação aos códigos anteriores, este não legisla quando já existem normas técnicas, evitando sobreposições de normas, além de possibilitar que o profissional habilitado exerça seu oficio com responsabilidade.")]),_vm._v(" "),