a se viver.")]),_vm._v(" "),_c('h2',[_vm._v("Como foi o processo de revisão?")]),_vm._v(" "),_c('p',[_vm._v("Considerando que o código anterior encontra-se em vigor há 25 anos e com base na experiência prática da sua aplicação, a Prefeitura de São Paulo iniciou um processo de avaliação e atualização dessas normas, tendo em vista oferecer resposta a novas demandas relacionadas à produção e consumo do ambiente construído, bem como adequação ao marco atual da disciplina urbanística, inaugurado com a aprovação do novo Plano Diretor Estratégico – PDE.")]),_vm._v(" "),_c('p',[_vm._v("A primeira providência desse trabalho foi a elaboração e consolidação de todas as alterações e inclusões promovidas no Código de Obras e Edificações, desde junho de 1992 até o presente. ")]),_vm._v(" "),_vm._m(1),_vm._v(" "),_vm._m(2),_vm._v(" "),_c('p',[_vm._v("Em paralelo, realizaram-se rodadas de discussão com entidades representativas de profissionais ligados à engenharia e arquitetura, do setor imobiliário e da construção civil, que agregam a maior parte do público que, necessariamente, utiliza o COE na elaboração dos projetos submetidos ao licenciamento, em especial: Associação Brasileira dos Escritórios de Arquitetura (ASBEA), Sindicato das Empresas de Compra, Venda, Locação e Administração de Imóveis Residenciais e Comerciais de São Paulo (SECOVI-SP) e Sindicato da Indústria da Construção Civil do Estado de São Paulo (SINDUSCON-SP).")]),_vm._v(" "),_vm._m(3),_vm._v(" "),_c('div',{staticClass:"ai2html",attrs:{"id":"g-00_apresentacao_d01-box"}},[_c('div',{staticClass:"g-artboard g-artboard-v4",attrs:{"id":"g-00_apresentacao_d01-01","data-min-width":"960"}},[_c('img',{staticClass:"g-aiImg",attrs:{"id":"g-00_apresentacao_d01-01-img","src":_vm.caminho + '00_apresentacao_d01-01.png'}}),_vm._v(" "),_vm._m(4),_vm._v(" "),_vm._m(5),_vm._v(" "),_vm._m(6),_vm._v(" "),_vm._m(7),_vm._v(" "),_vm._m(8),_vm._v(" "),_vm._m(9),_vm._v(" "),_vm._m(10),_vm._v(" "),_vm._m(11),_vm._v(" "),_vm._m(12),_vm._v(